.nav_bar {
  width: 100%;
  height: 40px;
  display: flex;
  -moz-box-pack: justify;
  justify-content: space-between;
  border-bottom: 1px solid darkgrey;
  text-align: left;

  & .nav_item {
    text-transform: capitalize;
    width: 23%;

    &:last-child {
      width: 8%;
    }
  }
}