$linkColor: black;

.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;

  .logo-container {
    height: 100%;
    width: 70px;
    padding: 25px;
  }

  .options {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .option {
      color: $linkColor;
      padding: 10px 15px;
      cursor: pointer;
      text-transform: uppercase;
      font-weight: 300;
      text-decoration: none;

      &:visited {
        color: $linkColor;
      }
    }

    .basket {
      height: 30px;
    }
  }
}
